<template>
    <p-data-table
        :value="cyclesSummary"
        dataKey="cycleId"
        v-model:selection="selectedRow"
        selectionMode="single"
        @row-select="onRowSelect"
        class="p-datatable-sm p-mt-1 p-shadow-1 p-mb-2"
        :scrollable="true"
        scrollHeight="flex"
        sortField="cycleDate"
        :sortOrder="-1"
        :loading="loading"
    >
        <p-column
            headerStyle="width: 3em; max-width: 3rem; min-width: 3rem"
            style="width: 3em; max-width: 3rem"
        >
            <template #header>
                <p-checkbox
                    v-model="selectAll"
                    :binary="true"
                    @change="selectAllOnChange"
                />
            </template>

            <template #body="{data}">
                <p-checkbox
                    v-model="checkList"
                    :name="data.cycleId"
                    :value="data.cycleId"
                />
            </template>
        </p-column>

        <p-column header="" style="max-width: 3rem; min-width: 2.7rem">
            <template #body="{ data }">
                <template v-if="selectedRow">
                    <p-button
                        v-if="selectedRow.cycleId == data.cycleId"
                        icon="pi pi-pencil"
                        class="p-button-sm p-button-secondary"
                        @click="showEditDailog"
                    />
                </template>
            </template>
        </p-column>
        <p-column
            field="cycleIdDisp"
            header="Cycle Id"
            style="width: 100px; max-width: 110px; min-width: 100px;"
        >
            <template #body="{ data }">
                <div>
                    {{ data.cycleIdDisp }}
                </div>
            </template>
        </p-column>
        <p-column
            field="cycleNo"
            header="Cycle No"
            style="width: 70px; max-width: 80px; min-width: 70px;"
        />
        <p-column
            field="cycleTypeDisp"
            header="Cycle Type"
            style="width: 160px; max-width: 170px; min-width: 160px;"
        />
        <p-column
            field="cycleNoType"
            header="Cycle No Type"
            style="width: 100px; max-width: 110px; min-width: 100px;"
        />
        <p-column
            field="ohip"
            header="Funded"
            style="width: 70px; max-width: 80px; min-width: 70px;"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.ohip"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="cancel"
            header="Cancel"
            style="width: 70px; max-width: 80px; min-width: 70px;"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.cancel"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="cycleCompleted"
            header="Complete"
            style="width: 70px; max-width: 80px; min-width: 70px;"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.cycleCompleted"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="surrogate"
            header="Surrogate"
            style="width: 70px; max-width: 80px; min-width: 70px;"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.surrogate"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="cycleDate"
            header="Cycle Date"
            style="width: 80px; max-width: 90px; min-width: 80px;"
        >
            <template #body="{ data }">
                {{ showFormatedDate(data.cycleDate.split(" ")[0]) }}
            </template>
        </p-column>
        <!-- <p-column
            field="incomplete"
            header="Incompletes"
            style="max-width: 7rem"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.incomplete"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column> -->
        <!-- <p-column
            field="cycleCompleted"
            header="Complete"
            style="max-width: 7rem"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.cycleCompleted"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column> -->
        <p-column
            field="spermSource"
            header="Sperm Source"
            style="width: 100px; max-width: 110px; min-width: 100px;"
        />
        <p-column
            field="eggSource"
            header="Egg Source"
            style="width: 80px; max-width: 90px; min-width: 80px;"
        />
        <p-column
            field="eggDonor"
            header="Egg Donor"
            style="width: 80px; max-width: 90px; min-width: 80px;"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.eggDonor"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>

        <p-column
            header="Donation"
            style="width: 80px; max-width: 90px; min-width: 80px;"
        >
            <template #body="{ data }">
                {{ data.numOfDonations }}
            </template>

            <!-- <template #body="{data}">
                <p-button
                    v-if="!data.numOfDonations || data.numOfDonations == 0"
                    label="Donation"
                    @click="openDonationDailog(data.cycleId)"
                    class="p-button-sm"
                    style="background-color: lightgray; color: lightgray; border-color: lightgray;"
                />
                <p-button
                    v-else
                    @click="openDonationDailog(data.cycleId)"
                    :badge="data.numOfDonations.toString()"
                    class="p-button-sm"
                >
                    {{ data.numOfDonations}}
                <p-badge :value="data.numOfDonations" />
                </p-button>
            </template> -->
        </p-column>
        <!-- <p-column field="no_fetus" header="Fetus" style="width: 80px; max-width: 90px;">
            <template #body="{ data }">
                {{ data.no_fetus }}
            </template>
        </p-column> -->

        <p-column
            field="gestationalSac"
            header="GS"
            style="width: 60px; max-width: 70px; min-width: 60px;"
        >
            <template #body="{ data }">
                {{ data.gestationalSac }}
            </template>
        </p-column>

        <p-column
            field="heartBeat"
            header="HB"
            style="width: 60px; max-width: 70px; min-width: 60px;"
        >
            <template #body="{ data }">
                {{ data.heartBeat }}
            </template>
        </p-column>

        <p-column
            field="lossFetus"
            header="Loss"
            style="width: 60px; max-width: 70px; min-width: 60px;"
        >
            <template #body="{ data }">
                {{ data.lossFetus }}
            </template>
        </p-column>

        <p-column style="width: 80px; max-width: 90px; min-width: 80px;">
            <template #body="{data}">
                <p-button
                    v-if="
                        hasBornCSVPermission &&
                            data.isBornCycle &&
                            data.bornCsvUploadStatus == 'NO'
                    "
                    label="Born CSV"
                    class="p-button-sm p-button-help"
                    @click="getBornCSV(data.cycleId, data.patientId)"
                />
                <p-button
                    v-if="
                        hasBornCSVPermission &&
                            data.isBornCycle &&
                            data.bornCsvUploadStatus == 'YES'
                    "
                    label="Born CSV"
                    class="p-button-sm p-button-success"
                    @click="getBornCSV(data.cycleId, data.patientId)"
                />
            </template>
        </p-column>

        <p-column
            field="isCsvComplete"
            header=""
            style="width: 80px; max-width: 90px; min-width: 80px;"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-if="hasBornCSVPermission && !!data.bornCycleId"
                    v-model="data.isCsvComplete"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>

        <template #footer>
            <div class="p-d-flex">
                <div class="p-field-checkbox p-mr-4">
                    <p-checkbox
                        v-model="hepatitisBPos"
                        name="hepatitisBPos"
                        :binary="true"
                    />
                    <label for="hepatitisBPos">Hepatitis B Positive</label>
                </div>
                <div class="p-field-checkbox p-mr-4">
                    <p-checkbox
                        v-model="hepatitisCPos"
                        name="hepatitisCPos"
                        :binary="true"
                    />
                    <label for="hepatitisCPos">Hepatitis C Positive</label>
                </div>

                <div class="p-field-checkbox p-mr-4">
                    <p-checkbox v-model="hivPos" name="hivPos" :binary="true" />
                    <label for="hivPos">HIV Positive</label>
                </div>

                <!-- Update button -->
                <p-button
                    label="Update"
                    class="p-mr-6 p-p-2 p-button-sm"
                    style="height: 24px;"
                    @click="updateChecks"
                />
            </div>
        </template>
    </p-data-table>
    <div class="footer-buttons">
        <p-button
            label="New Cycle"
            @click="newCycle"
            v-if="hasCreatePermission"
        />
        <p-button label="Merge Cycle" @click="mergeCycle" />
        <p-button
            label="Delete Cycle"
            @click="deleteCycleConfirm"
            v-if="hasDeletePermission"
        />
        <p-button label="PDF Report" @click="pdfReport" />
        <p-button label="Order Blood Test" @click="orderBloodTest" />
        <!-- <p-button label="Import HL7 Message" /> -->
        <p-button label="Cryostorage Report" @click="cryostorageReport" />
        <p-button label="Couples Rpt" @click="couplesReport" />
        <p-button
            label="Create Born"
            class="p-button-help"
            :loading="createBornLoading"
            v-if="isCreateBornCycle && hasCreatePermission"
            @click="createBornCycle"
        />
        <!-- <p-button
            label="Born Csv"
            class="p-mr-6 p-button-help"
            v-if="isBornCycle"
            @click="getBornCSV"
        /> -->
    </div>
    <div>
        <born-csv-error-dailog />
        <donation-dialog :cycleId="donationCycleId" />
    </div>
    <p-dialog
        v-model:visible="deleteDialogvisible"
        modal
        header="Delete Cycle!!"
        :style="{ width: '30rem' }"
    >
        <p>
            Are you sure? If yes, then type the cycleId again.
            <p-input-text v-model="deleteCycleNo" type="text" class="p-mt-2" />
        </p>
        <p-button label="No" class="p-mr-2" @click="cancelDeleteCycle" />
        <p-button label="Yes" class="p-button-danger" @click="deleteCycle" />
    </p-dialog>
</template>

<script>
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    provide,
    ref,
    reactive,
    watch
} from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { getBronCsvMulti, validateCsv } from "@/service/art-cycle";
import { ORIGIN } from "@/config";
import { usePermissionCheck } from "@/composable/users";
import { $axios } from "@/service/app-http";

import BornCsvErrorDailog from "@/components/born/born-csv-error-dialog.vue";
import DonationDialog from "@/components/art-cycle/donation-dialog.vue";
import { NL_FORM_BASE } from "@/constants";
import { showFormatedDate } from "@/service/utils";
import { useToast } from "primevue/usetoast";
import { useConfirm } from "primevue/useconfirm";

export default {
    components: { BornCsvErrorDailog, DonationDialog },
    setup() {
        const toast = useToast();
        const checkList = ref([]);
        const selectAll = ref(false);
        const selectAllOnChange = () => {
            if (selectAll.value) {
                checkList.value = cyclesSummary.value.map(
                    (item) => item.cycleId
                );
            } else {
                checkList.value = [];
            }
        };

        const checkScannedBarcode = async (_barcode) => {
            if (cyclesSummary.value) {
                const selectedCycleSummery = cyclesSummary.value.find(
                    (row) => row.cycleId.toString() === _barcode
                );

                // Output the result
                if (selectedCycleSummery) {
                    emitter.emit("cycles-ui:play-beep", 1);
                    // Example: Use toast to display the value
                    toast.add({
                        severity: "success",
                        summary: _barcode,
                        detail: "Barcode is correct. Confirmed!",
                        life: 3000
                    });

                    onRowSelect({ data: selectedCycleSummery });
                } else {
                    if (
                        store.state.artCycle.patient &&
                        store.state.artCycle.patient.chart == _barcode
                    ) {
                        emitter.emit("cycles-ui:play-beep", 1);
                        toast.add({
                            severity: "warn",
                            summary: _barcode,
                            detail: "Patient is correct. Confirmed!",
                            life: 3000
                        });

                        return;
                    }

                    emitter.emit("cycles-ui:play-beep", 0);
                    toast.add({
                        severity: "error",
                        summary: _barcode,
                        detail: "Barcode is not correct. Wrong!",
                        life: 3000
                    });

                    const scanarea = "art-cycle";

                    try {
                        const { data } = await $axios.get(
                            `scan-barcode/data?barcode=${_barcode}&scanarea=${scanarea}`
                        );
                        emitter.emit("cycles-ui:redict-to-other", data);
                    } catch (e) {
                        console.log(e);
                    }

                    // const anotherPatient = '105337';
                    // const pageName = 'blood-tests';
                }
            }
            // Example: Log the value
            // console.log("Scanned barcode:", _barcode);
        };

        const deleteDialogvisible = ref(false);
        const deleteCycleNo = ref(null);

        const store = useStore();
        const loading = inject("loading", false);
        const createOrEditDialog = inject("createOrEditDialog");
        const isEditDailog = inject("isEditDailog");
        const mergeCycleDailog = inject("mergeCycleDailog");
        const orderBloodTestConfirmDialog = inject(
            "orderBloodTestConfirmDialog"
        );
        const cryostorageReportDailog = inject("cryostorageReportDailog");
        const couplesReportDialog = inject("couplesReportDialog");

        const selectedRow = ref();
        const cyclesSummary = computed(
            () => store.state.artCycle.cyclesSummary
        );
        const isCreateBornCycle = computed(
            () => store.getters["artCycle/isCreateBornCycle"]
        );

        const onRowSelect = ({ data }) => {
            store.commit("artCycle/SET_SELECTED_CYCLE", data);
            store.dispatch("born/getBronV2", data);
        };

        const setFirstOneSelected = (_cycles) => {
            let topCycleIndex = _cycles.findIndex(
                (item) => item.cycleNo == _cycles.length
            );
            selectedRow.value = cyclesSummary.value[topCycleIndex];
            store.commit("artCycle/SET_SELECTED_CYCLE", selectedRow.value);
            store.dispatch("born/getBronV2", { ...selectedRow.value });
        };

        const save = () => {
            emitter.emit("cycle-table:save-cycle");
        };

        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );

        const newCycle = () => {
            if (cyclesSummary.value && cyclesSummary.value.length > 0) {
                let lCYcle = cyclesSummary.value[0];

                if (!lCYcle.cancel && !lCYcle.cycleCompleted) {
                    toast.add({
                        severity: "error",
                        summary: "",
                        detail:
                            "You can not create a new cycle. Because, Previous Cycle is not Complete / Cancelled",
                        life: 3000
                    });

                    return;
                }
            }
            isEditDailog.value = false;
            createOrEditDialog.value = true;
            return;
            /*loading.value = true;
            store
                .dispatch("artCycle/createNewCycle")
                .then(() => (loading.value = false)); */
        };

        const confirm = useConfirm();

        const cancelDeleteCycle = () => {
            deleteDialogvisible.value = false;
        };

        const deleteCycleConfirm = () => {
            deleteDialogvisible.value = true;
            deleteCycleNo.value = null;
        };

        const deleteCycle = () => {
            if (selectedCycle.value.cycleId == deleteCycleNo.value) {
                loading.value = true;
                store
                    .dispatch(
                        "artCycle/deleteCycle",
                        selectedCycle.value.cycleId
                    )
                    .then(() => {
                        toast.add({
                            severity: "success",
                            summary: "",
                            detail: "Deleted successfully",
                            life: 3000
                        });
                        loading.value = false;
                        deleteDialogvisible.value = false;
                    });
            } else {
                toast.add({
                    severity: "error",
                    summary: "",
                    detail: "CycleId did not match with the selected CycleId",
                    life: 3000
                });
            }
        };

        const createBornLoading = ref(false);
        const createBornCycle = () => {
            createBornLoading.value = true;
            const cycle = store.state.artCycle.selectedCycle;
            const born = {
                cycleId: cycle.cycleId,
                cycleName: cycle.cycleType,
                pNumber: cycle.patientId,
                createdBy: "lana",
                updatedBy: "lana",
                parentCycleType: "a",
                cycleNumber: cycle.cycleNo,
                firstName: store.state.artCycle.patient.firstName,
                lastName: store.state.artCycle.patient.lastName,
                sex: "f",
                yearOfBirth: store.state.artCycle.patient.year,
                monthOfBirth: store.state.artCycle.patient.month,
                dateOfBirth: store.state.artCycle.patient.day
            };

            store
                .dispatch("artCycle/createNewBornCycle", born)
                .then((data) => {
                    return store.dispatch(
                        "artCycle/getCyclesDetails",
                        store.state.artCycle.patient.id
                    );
                })
                .catch(() => {
                    createBornLoading.value = false;
                });
        };

        const getCycleDetails = (
            demographicNo = store.state.artCycle.selectedCycle.patientId
        ) => {
            console.log("getCycleDetails");
            loading.value = true;
            store
                .dispatch("artCycle/getCyclesDetails", demographicNo)
                .then(() => (loading.value = false));
        };

        const bornCsvErrorDailog = ref(false);
        provide("bornCsvErrorDailog", bornCsvErrorDailog);
        const bornCsvErrors = ref([]);
        provide("bornCsvErrors", bornCsvErrors);
        const bornCsvCycle = reactive({
            cycleId: null,
            patientId: null
        });

        const isBornCycle = computed(
            () => store.getters["artCycle/isBornCycle"]
        );
        const getBornCSV = async (cycleId, patientId) => {
            bornCsvCycle.cycleId = cycleId;
            bornCsvCycle.patientId = patientId;
            try {
                const resp = await validateCsv(
                    store.state.artCycle.patient.id,
                    [cycleId]
                );
            } catch (err) {
                if (err.response.status == 400) {
                    bornCsvErrors.value = err.response.data;
                    bornCsvErrorDailog.value = true;
                }
                return;
            }

            bornCsvErrors.value = null;
            bornCsvErrorDailog.value = true;
        };

        const downloadCsv = async (id) => {
            bornCsvErrorDailog.value = false;
            const payload = {
                cycle: [{ ...bornCsvCycle }]
            };

            const resp = await getBronCsvMulti(payload);
            if (resp.status == "failed") return;

            window.open(`${ORIGIN}/${resp.csv}`);
        };

        const {
            hasCreatePermission,
            hasDeletePermission,
            hasBornCSVPermission
        } = usePermissionCheck();

        const donationCycleId = ref();
        const cycleDonationDialog = ref(false);
        provide("cycleDonationDialog", cycleDonationDialog);

        watch(selectedCycle, (newValue) => {
            selectedRow.value = newValue;
        });

        watch(cyclesSummary, (newValue) => {
            checkList.value = [];
            console.log(checkList.value);
        });

        onMounted(() => {
            selectedRow.value = store.state.artCycle.selectedCycle;
            emitter.on("cycles-ui:check-barcode", checkScannedBarcode);
            emitter.on("merge-cycle-dialog:merge-cycle", getCycleDetails);
            emitter.on("born-csv-error-diallog:get-bron-csv", downloadCsv);
        });

        onUnmounted(() => {
            emitter.off("cycles-ui:check-barcode", checkScannedBarcode);
            emitter.off("merge-cycle-dialog:merge-cycle", getCycleDetails);
            emitter.off("born-csv-error-diallog:get-bron-csv", downloadCsv);
        });

        // Computed properties to sync with Vuex store
        const hepatitisBPos = computed({
            get: () => store.state.artCycle.patient.hepatitisBPos,
            set: (value) => store.commit("artCycle/setHepatitisBPos", value)
        });

        const hepatitisCPos = computed({
            get: () => store.state.artCycle.patient.hepatitisCPos,
            set: (value) => store.commit("artCycle/setHepatitisCPos", value)
        });

        store.state.artCycle.patient.hivPos;
        const hivPos = computed({
            get: () => store.state.artCycle.patient.hivPos,
            set: (value) => store.commit("artCycle/setHivPos", value)
        });

        // Method to update the patient checks
        const updateChecks = async () => {
            const patientId = store.state.artCycle.patient.id;

            const paramPayload = {
                hepatitisB: hepatitisBPos.value,
                hepatitisC: hepatitisCPos.value,
                hiv: hivPos.value,
                patientId: patientId
            };

            try {
                const { response } = await $axios.post(
                    `patient-lab-test/save`,
                    paramPayload
                );

                const PATIENT_KEY = "PATIENT";
                localStorage.setItem(
                    PATIENT_KEY,
                    JSON.stringify({ ...store.state.artCycle.patient })
                );

                toast.add({
                    severity: "success",
                    summary: _barcode,
                    detail: "Lab Test Saved",
                    life: 3000
                });
            } catch (error) {
                console.error("An error occurred while updating:", error);
            }
        };

        return {
            cancelDeleteCycle,
            deleteCycleConfirm,
            deleteDialogvisible,
            deleteCycleNo,
            deleteCycle,
            toast,
            checkList,
            selectAll,
            selectAllOnChange,
            cyclesSummary,
            selectedRow,
            onRowSelect,
            save,
            newCycle,
            loading,
            isCreateBornCycle,
            createBornCycle,
            createBornLoading,
            createOrEditDialog,
            showEditDailog: () => {
                isEditDailog.value = true;
                createOrEditDialog.value = true;
            },
            mergeCycle: () => {
                mergeCycleDailog.value = true;
            },
            orderBloodTest: () => {
                orderBloodTestConfirmDialog.value = true;
            },
            pdfReport: () => {
                if (checkList.value.length > 0) {
                    const cyclesId = checkList.value.join();
                    let url =
                        `${NL_FORM_BASE}/cycles-reports/print-cycle-monitoring.php` +
                        `?cycleId=${cyclesId}` +
                        `&patientId=${store.state.artCycle.selectedCycle.patientId}`;
                    // + `&apiToken=${store.state.user.accessToken}`;
                    window.open(url, "_blank");
                } else {
                    let url =
                        `${NL_FORM_BASE}/cycles-reports/print-cycle-monitoring.php` +
                        `?cycleId=${store.state.artCycle.selectedCycle.cycleId}` +
                        `&patientId=${store.state.artCycle.selectedCycle.patientId}`;
                    // +  `&apiToken=${store.state.user.accessToken}`;
                    window.open(url, "_blank");
                }
            },
            cryostorageReport: () => {
                cryostorageReportDailog.value = true;
            },
            couplesReport: () => {
                couplesReportDialog.value = true;
            },
            isBornCycle,
            getBornCSV,
            hasCreatePermission,
            hasDeletePermission,
            hasBornCSVPermission,
            donationCycleId,
            openDonationDailog: (cycleId) => {
                // donationCycleId.value = cycleId;
                // cycleDonationDialog.value = true;
            },
            showFormatedDate,

            hepatitisBPos,
            hepatitisCPos,
            hivPos,
            updateChecks
        };
    }
};
</script>

<style scoped>
:deep() .p-selectable-row {
    font-size: 14px;
}

:deep() .p-datatable-tbody > tr > td {
    padding: 0.4rem 0.5rem;
}

.p-button-sm {
    padding: 0.1rem;
}

.footer-buttons {
    display: flex;
    gap: 2px;
}

@media (max-width: 768px) {
    .footer-buttons {
        display: flex;
        flex-direction: column;
        gap: 2px;
    }
    .footer-buttons .p-button {
        width: 100%;
    }
}
</style>
