<template>
    <p-card class="p-mt-2">
        <template #content>
            <div class="p-grid" style="margin-top: -20px">
                <div class="p-col-fixed p-md-4">
                    <div class="p-fluid p-grid">
                        <p-divider align="left">
                            <b>Obstetrical History</b>
                        </p-divider>
                        <div class="p-col">
                            <div class="p-field p-grid">
                                <label class="p-col-fixed p-mb-0">
                                    Prior Pregnancy
                                </label>
                                <div class="p-col-fixed">
                                    <!-- <p-dropdown
                                        v-model="form.priorPregnancies"
                                        :options="priorPregnanciesOptions"
                                        optionLabel="label"
                                        optionValue="value"
                                    /> -->
                                    <p-input-text
                                        v-model="form.priorPregnancies"
                                        type="number"
                                        style="width: 80px"
                                        :class="{
                                            'p-invalid':
                                                v$.priorPregnancies.$invalid
                                        }"
                                        min="0"
                                    />
                                </div>
                            </div>
                        </div>
                        <div class="p-col">
                            <div class="p-field p-grid">
                                <label class="p-col-fixed p-mb-0">
                                    Prior Births
                                </label>
                                <div class="p-col">
                                    <p-input-text
                                        v-model="form.priorBirths"
                                        style="width: 60px"
                                        :class="{
                                            'p-invalid': v$.priorBirths.$invalid
                                        }"
                                        type="number"
                                        min="0"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-md-8">
                    <div class="p-fluid p-grid">
                        <p-divider align="left">
                            <b>Prios ART Treatment</b>
                        </p-divider>
                        <div class="p-col-fixed p-md-4">
                            <div class="p-field p-grid">
                                <label class="p-col-fixed p-mb-0">
                                    Prior fresh cycles started, own oocytes
                                </label>
                                <div class="p-col">
                                    <p-input-text
                                        v-model="form.priorFreshCyclesStarted"
                                        type="number"
                                        min="0"
                                    />
                                </div>
                            </div>
                        </div>
                        <div class="p-col-fixed p-md-4">
                            <div class="p-field p-grid">
                                <label class="p-col-fixed p-mb-0">
                                    Prior ETs (fresh or frozen), own oocytes
                                </label>
                                <div class="p-col">
                                    <p-input-text
                                        v-model="form.priorETsOwnOocytes"
                                        type="number"
                                        min="0"
                                    />
                                </div>
                            </div>
                        </div>
                        <div class="p-col-fixed p-md-4">
                            <div class="p-field p-grid">
                                <label class="p-col-fixed p-mb-0">
                                    Prior ETs (fresh or frozen), donor oocytes
                                </label>
                                <div class="p-col">
                                    <p-input-text
                                        v-model="form.priorETsDonorOocytes"
                                        type="number"
                                        min="0"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <p-divider align="left" style="margin-top: 0">
                <b>Patient Funding Status</b>
            </p-divider>
            <div class="p-fluid p-formgrid p-grid">
                <div class="p-col-fixed p-md-2">
                    <div class="p-field p-grid" style="min-width: 250px">
                        <label class="p-col-fixed">
                            Provincial Funding
                        </label>
                        <div class="p-col-fixed p-formgroup-inline">
                            <div class="p-field-checkbox">
                                <p-radio-button
                                    name="provincial-funding"
                                    value="yes"
                                    v-model="form.provincialFundingForThisCycle"
                                    :class="{
                                        'p-invalid':
                                            v$.provincialFundingForThisCycle
                                                .$invalid
                                    }"
                                />
                                <label>Yes</label>
                            </div>
                            <div class="p-field-checkbox">
                                <p-radio-button
                                    name="provincial-funding"
                                    value="no"
                                    v-model="form.provincialFundingForThisCycle"
                                    :class="{
                                        'p-invalid':
                                            v$.provincialFundingForThisCycle
                                                .$invalid
                                    }"
                                />
                                <label>NO</label>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0"> Funding Type </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.typeOfFunding"
                                :options="typeOfFundingOptions"
                                optionLabel="label"
                                optionValue="value"
                                :class="{
                                    'p-invalid': v$.typeOfFunding.$invalid
                                }"
                                :disabled="
                                    form.provincialFundingForThisCycle == 'no'
                                "
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Other Specify
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.otherSpecify"
                                :disabled="
                                    form.provincialFundingForThisCycle == 'no'
                                "
                            />
                        </div>
                    </div>
                </div>
                <!-- <div class="p-col-fixed p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Origin simulated cycle
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.originOfSimulatedCycle"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Simulated primary cycle
                        </label>
                        <p-toggle-button
                            v-model="form.primaryCycle"
                            :binary="true"
                            onIcon="pi pi-check"
                            offIcon="pi pi-times"
                        />
                    </div>
                </div> -->
            </div>

            <p-divider align="left" style="margin-top: 0">
                <b>Infertillity Diagnosis</b>
            </p-divider>

            <div class="p-formgroup-inline">
                <div class="p-field">
                    <label class="p-mb-0">
                        Reason for Treatment Cycle
                    </label>
                    <p-multi-select
                        v-model="form.reasonsForTreatmentCycle"
                        :options="reasonsForTreatmentCycleOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width: 250px"
                        :class="{
                            'p-invalid': v$.reasonsForTreatmentCycle.$invalid
                        }"
                    />
                    <i
                        v-if="v$.reasonsForTreatmentCycle.$invalid"
                        class="pi pi-info-circle p-ml-2"
                        v-tooltip.right="
                            v$.reasonsForTreatmentCycle.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <label class=" p-mb-0">
                        Reason for Male Factor
                    </label>
                    <p-multi-select
                        v-model="form.reasonsForMaleFactor"
                        :options="reasonsForMaleFactorOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width: 250px"
                        :class="{
                            'p-invalid': v$.reasonsForMaleFactor.$invalid
                        }"
                    />
                    <i
                        v-if="v$.reasonsForMaleFactor.$invalid"
                        class="pi pi-info-circle p-ml-2"
                        v-tooltip.right="
                            v$.reasonsForMaleFactor.validator.$message
                        "
                        style="color: #f44336"
                    ></i>
                </div>

                <div class="p-field">
                    <div class="p-formgroup-inline">
                        <label class="p-mt-1 p-mr-1" style="color: black">
                            PGTA Planned
                        </label>
                        <div class="p-field-checkbox">
                            <p-radio-button
                                name="provincial-funding"
                                value="Y"
                                v-model="form.pgtAPlanned"
                            />
                            <label>Yes</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-radio-button
                                name="provincial-funding"
                                value="N"
                                v-model="form.pgtAPlanned"
                            />
                            <label>NO</label>
                        </div>
                    </div>
                </div>
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
                :disabled="v$.$invalid"
            />
        </template>
    </p-card>
</template>

<script>
import { inject, onMounted, onUnmounted, toRefs, watch } from "vue";
import { useObstetricalHistoryFormInit } from "@/composable/born";
import { emitter } from "@/event-bus";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            fillForm,
            v$,
            save,
            loading,
            priorPregnanciesOptions,
            priorBirthsOptions,
            priorFreshCyclesStartedOptions,
            priorETsOwnOocytesOptions,
            priorETsDonorOocytesOptions,
            typeOfFundingOptions,
            reasonsForMaleFactorOptions,
            reasonsForTreatmentCycleOptions
        } = useObstetricalHistoryFormInit();

        const { isInBornPage } = toRefs(props);

        watch(() => store.state.born.obstetricalHistory, fillForm);

        const bornForm = inject("bornForm", null);
        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.obstetricalHistory = { ...newValue };
        });

        watch(
            () => form.reasonsForTreatmentCycle,
            (newVal) => {
                store.commit("born/SET_REASON_FOR_TREATMENT_CYCLE", newVal);
            }
        );

        // watch(
        //     () => form.priorPregnancies,
        //     (newVal) => {
        //         if (newVal == 0) {
        //             form.priorBirths = 0;
        //         } else {
        //             form.priorBirths = null;
        //         }
        //     }
        // );

        watch(
            () => form.provincialFundingForThisCycle,
            (newVal) => {
                if (newVal == "no") {
                    form.typeOfFunding = null;
                    form.otherSpecify = null;
                } else {
                    form.typeOfFunding = "Ontario MoH transfer payment";
                }
            }
        );

        watch(
            () => form.pgtAPlanned,
            (newVal) => {
                store.commit("born/SET_PGT_A_PLANNED", newVal);
            }
        );

        const setReasonForTreatmentCycleDefault = (value) => {
            if (form.reasonsForTreatmentCycle.includes(value)) return;

            form.reasonsForTreatmentCycle = [
                ...form.reasonsForTreatmentCycle,
                value
            ];
        };

        onMounted(() => {
            emitter.on("born:save-temp", save);
            emitter.on(
                "daignosis-form:on-diagnosis-select",
                setReasonForTreatmentCycleDefault
            );

            if (isInBornPage.value) {
                return;
            }

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
            emitter.off(
                "daignosis-form:on-diagnosis-select",
                setReasonForTreatmentCycleDefault
            );
        });

        return {
            isInBornPage,
            form,
            v$,
            save,
            loading,
            priorPregnanciesOptions,
            priorBirthsOptions,
            priorFreshCyclesStartedOptions,
            priorETsOwnOocytesOptions,
            priorETsDonorOocytesOptions,
            typeOfFundingOptions,
            reasonsForMaleFactorOptions,
            reasonsForTreatmentCycleOptions
        };
    }
};
</script>
