<template>
    <div class="p-mt-6 container">
        <!-- Fixed Header Row -->
        <div class="grid-header-row">
            <div class="grid-cell date-cell">Date</div>
            <div class="grid-cell comment-cell">Comment</div>
            <div class="grid-cell actions-cell">
                <p-button
                    label="Add Consult"
                    @click="openModal(null)"
                    class="p-mr-2 p-button-primary p-button-sm"
                />
            </div>
        </div>

        <!-- Scrollable Body Rows -->
        <div class="grid-body">
            <div v-for="consult in consults" :key="consult.id" class="grid-row">
                <div class="grid-cell date-cell">
                    {{ consult.consult_date }}
                </div>
                <div class="grid-cell comment-cell">
                    {{ consult.comment }}
                </div>
                <div class="grid-cell actions-cell">
                    <p-button
                        icon="pi pi-pencil"
                        @click="openModal(consult)"
                        class="p-button-text p-mr-2"
                    />
                    <p-button
                        icon="pi pi-trash"
                        class="p-button-danger p-button-text"
                        @click="deleteEntry(consult)"
                    />
                </div>
            </div>
        </div>

        <!-- Dialog Modal for Add/Edit Consult -->
        <p-dialog
            header="Add Consult"
            v-model:visible="displayModal"
            :modal="true"
            :closable="false"
            :draggable="false"
            :resizable="false"
            style="width: 1000px;"
        >
            <div class="p-fluid">
                <div class="field" style="margin-bottom: 1rem;">
                    <label
                        for="date"
                        style="display: block; margin-bottom: 0.5rem;"
                        >Date</label
                    >
                    <p-calendar
                        v-model="entry.consult_date"
                        id="date"
                        showIcon
                        dateFormat="yy-mm-dd"
                        :showButtonBar="true"
                    />
                </div>

                <div class="field">
                    <label
                        for="comment"
                        style="display: block; margin-bottom: 0.5rem;"
                        >Comments</label
                    >
                    <p-textarea
                        v-model="entry.comment"
                        id="comment"
                        placeholder="Enter comment"
                        style="width: 100%;"
                    />
                </div>
            </div>
            <template #footer>
                <p-button
                    label="Cancel"
                    icon="pi pi-times"
                    class="p-button-text"
                    @click="closeModal"
                />
                <p-button
                    label="Save"
                    icon="pi pi-check"
                    class="p-button-text"
                    @click="saveEntry"
                />
            </template>
        </p-dialog>
    </div>
</template>

<script>
import { ref, computed } from "vue";
import { useStore } from "vuex";
import { DATE_FORMAT_STRING } from "@/config";
import format from "date-fns/format";
import { $axios } from "@/service/app-http";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const store = useStore();
        const toast = useToast();

        const consults = computed({
            get: () => store.state.artCycle.patient.consults,
            set: (value) => store.commit("artCycle/setConsults", value)
        });

        const displayModal = ref(false);
        const entry = ref({ id: null, date: null, comment: "" });

        const openModal = (existingEntry = null) => {
            if (
                store.state.artCycle.patient &&
                store.state.artCycle.patient.id
            ) {
                if (existingEntry) {
                    entry.value = { ...existingEntry };
                } else {
                    entry.value = { id: null, date: null, comment: "" };
                }
                displayModal.value = true;
            }
        };

        const closeModal = () => {
            displayModal.value = false;
            entry.value = { id: null, date: "", comment: "" };
        };

        const saveEntry = async () => {
            const formattedToDate = format(
                new Date(entry.value.consult_date),
                DATE_FORMAT_STRING
            );

            entry.value.consult_date = formattedToDate;

            try {
                if (entry.value.id) {
                    const { data } = await $axios.put(
                        `patient-consult/update/${entry.value.id}`,
                        {
                            ...entry.value,
                            patientId: store.state.artCycle.patient.id,
                            consultDate: entry.value.consult_date
                        }
                    );

                    // console.log(response);
                    const index = consults.value.findIndex(
                        (e) => e.id === entry.value.id
                    );

                    if (index !== -1) consults.value[index] = { ...data.data };
                } else {
                    const { data } = await $axios.post(`patient-consult/add`, {
                        ...entry.value,
                        patientId: store.state.artCycle.patient.id,
                        consultDate: entry.value.consult_date
                    });

                    // console.log(response);
                    consults.value.push({ ...data.data });
                }

                const PATIENT_KEY = "PATIENT";
                localStorage.setItem(
                    PATIENT_KEY,
                    JSON.stringify({ ...store.state.artCycle.patient })
                );

                toast.add({
                    severity: "success",
                    summary: _barcode,
                    detail: "Date Saved",
                    life: 3000
                });
            } catch (error) {
                console.error("An error occurred while updating:", error);
            }

            // if (entry.value.id) {
            //     const index = consults.value.findIndex(
            //         (e) => e.id === entry.value.id
            //     );
            //     if (index !== -1) consults.value[index] = { ...entry.value };
            // } else {
            //     entry.value.id = new Date().getTime();
            //     consults.value.push({ ...entry.value });
            // }

            // console.log(entry);
            closeModal();
        };

        const deleteEntry = async (entryToDelete) => {
            if (confirm("Are you Sure to delete the Consult Comments Line?")) {
                try {
                    const { response } = await $axios.delete(
                        `patient-consult/delete/${entryToDelete.id}`,
                        entryToDelete
                    );

                    consults.value = consults.value.filter(
                        (e) => e.id !== entryToDelete.id
                    );

                    const PATIENT_KEY = "PATIENT";
                    localStorage.setItem(
                        PATIENT_KEY,
                        JSON.stringify({ ...store.state.artCycle.patient })
                    );
                } catch (error) {
                    console.error("An error occurred while deleting:", error);
                }
            }
        };

        return {
            consults,
            displayModal,
            entry,
            openModal,
            closeModal,
            saveEntry,
            deleteEntry
        };
    }
};
</script>

<style scoped>
.container {
    margin: 0 auto;
}
.grid-header-row {
    display: grid;
    grid-template-columns: 150px 1fr 150px;
    background-color: #f1f1f1;
    font-weight: bold;
    border-bottom: 1px solid #ddd;
}
.grid-body {
    display: grid;
    grid-template-columns: 150px 1fr 150px;
    gap: 0.5rem;
    height: calc(
        ((100vh - 182px) * 0.25)
    ); /* Fixed height for scrollable content */
    overflow-y: auto;
}
.grid-row {
    display: contents;
}
.grid-cell {
    padding: 0.5rem;
    border-bottom: 1px solid #ddd;
    display: block; /* Ensure cell height is auto-adjusted */
    white-space: normal; /* Allow text wrapping */
    overflow-wrap: break-word; /* Break long words */
}
.date-cell {
    justify-content: flex-start;
}
.comment-cell {
    justify-content: flex-start;
}
.actions-cell {
    justify-content: flex-end;
}

@media (max-width: 768px) {
    .grid-body {
        display: grid;
        grid-template-columns: 150px 1fr 150px;
        gap: 0.5rem;
        height: 500px; /* Fixed height for scrollable content */
        overflow-y: auto;
    }
}
</style>
