<template>
    <p-card>
        <template #content>
            <div class="p-grid">
                <div class="p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label for="firstname" class="p-col-fixed p-mb-0">
                            Cycle Type
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="cycle.cycleType"
                                id="cycle-type"
                                :options="cycleTypeOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <p-toggle-button
                                v-model="cycle.ohip"
                                id="egg-donor"
                                onIcon="pi pi-check"
                                offIcon="pi pi-times"
                            />
                        </div>
                        <label for="firstname" class="p-col p-mb-0">
                            Funded
                        </label>
                    </div>
                </div>
                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <p-toggle-button
                                v-model="cycle.cancelled"
                                id="egg-donor"
                                onIcon="pi pi-check"
                                offIcon="pi pi-times"
                            />
                        </div>
                        <label for="firstname" class="p-col p-mb-0">
                            Cycle Cancelled
                        </label>
                    </div>
                </div>
                <div class="p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label for="firstname" class="p-col-fixed p-mb-0">
                            Sperm Source
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="cycle.spermSource"
                                id="cycle-type"
                                :filter="true"
                                :options="spermSourceOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label for="firstname" class="p-col-fixed p-mb-0">
                            Egg Source
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="cycle.eggSource"
                                id="cycle-type"
                                :filter="true"
                                :options="eggSourceOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </div>
                    </div>
                </div>
                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <p-toggle-button
                                v-model="cycle.eggDonor"
                                id="egg-donor"
                                onIcon="pi pi-check"
                                offIcon="pi pi-times"
                            />
                        </div>
                        <label for="firstname" class="p-col p-mb-0">
                            Egg Donor
                        </label>
                    </div>
                </div>
                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <div class="p-col-fixed">
                            <p-toggle-button
                                v-model="cycle.complete"
                                id="egg-donor"
                                onIcon="pi pi-check"
                                offIcon="pi pi-times"
                            />
                        </div>
                        <label for="firstname" class="p-col p-mb-0">
                            Complete
                        </label>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { onMounted, watch } from "vue";
import { emitter } from "@/event-bus";
import { useCycleCreateOrEditDailogInit } from "@/composable/art-cycle";

export default {
    setup() {
        const {
            store,
            selectedCycle,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle,
            fillupForm,
            loading
        } = useCycleCreateOrEditDailogInit();

        watch(selectedCycle, fillupForm);

        const saveCycle = () => {
            loading.value = true;
            store
                .dispatch("artCycle/updateCycle", {
                    cycleNo: selectedCycle.value.cycleNo,
                    donnations: selectedCycle.value.donnations,
                    cycleId: selectedCycle.value.cycleId,
                    cycleType: cycle.cycleType,
                    cycleNoType: cycle.cycleNoType,
                    spermSource: cycle.spermSource,
                    eggSource: cycle.eggSource,
                    eggDonor: !!cycle.eggDonor,
                    cycleIncomplete: !cycle.complete,
                    cycleComplete: !!cycle.complete,
                    ohip: cycle.ohip,
                    cycleCancelled: !!cycle.cancelled,
                    others: cycle.others,
                    cycleDate: selectedCycle.value.cycleDate
                })
                .then(() => {
                    return store.dispatch(
                        "artCycle/getCyclesDetails",
                        store.state.artCycle.patient.id
                    );
                })
                .catch(() => {})
                .finally(() => {
                    loading.value = false;
                });
        };

        onMounted(() => {
            if (selectedCycle.value) {
                fillupForm(selectedCycle.value);
            }
            emitter.on("cycle-table:save-cycle", () => saveCycle());
        });

        onMounted(() => {
            emitter.off("cycle-table:save-cycle", saveCycle);
        });

        return {
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle
        };
    }
};
</script>

<style scoped>
.p-dropdown {
    width: 100%;
}
</style>
