<template>
    <div class="female-search">
        <p-card>
            <template #content>
                <div class="p-fluid">
                    <div class="p-field p-grid">
                        <label
                            for="firstname"
                            class="p-col-fixed p-text-bold p-mb-2 p-md-2 p-mb-md-0"
                            style="width: 100px; font-size: 16px"
                        >
                            Female
                        </label>
                        <div class="p-col">
                            <p-auto-complete
                                v-model="demographic"
                                :suggestions="fillteredDemographic"
                                @complete="searchDemographic($event)"
                                @item-select="onDemographicSelect($event)"
                                field="label"
                                placeholder="Type to search"
                            />
                        </div>
                        <div class="p-col-fixed">
                            <span class="p-buttonset">
                                <p-button
                                    icon="pi pi-users"
                                    class="p-button-sm"
                                    v-tooltip.bottom="`Print patient & Partner`"
                                    @click="printPatientAndPartner"
                                />
                                <p-button
                                    icon="pi pi-user"
                                    class="p-button-sm"
                                    v-tooltip.bottom="`Print patient Detils`"
                                    @click="printPatientAndDoctor"
                                />
                            </span>
                        </div>
                    </div>
                </div>
            </template>
        </p-card>
    </div>
</template>

<script>
import { onMounted, onUnmounted } from "vue";

import { usePatientSearch } from "@/composable/art-cycle";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const {
            store,
            title,
            demographic,
            fillteredDemographic,
            searchDemographic,
            onDemographicSelect,
            emptyPatient,
            printPatientAndPartner,
            printPatientAndDoctor
        } = usePatientSearch();

        onMounted(() => {
            let patient = store.state.artCycle.patient;
            if (patient && patient.sex === "F") {
                demographic.value = {
                    ...patient,
                    label: `${patient.id} - ${patient.firstName} ${patient.lastName}`
                };
            }

            emitter.on("partner-search:demographic-select", emptyPatient);
        });

        onUnmounted(() => {
            emitter.off("partner-search:demographic-select", emptyPatient);
        });

        return {
            title,
            demographic,
            fillteredDemographic,
            searchDemographic,
            onDemographicSelect,
            printPatientAndPartner,
            printPatientAndDoctor
        };
    }
};
</script>
<style scoped>
@media (max-width: 768px) {
    .female-search {
        margin-top: 40px;
    }
}
</style>
