<template>
    <p-dialog
        :header="`Cycle - ${cycleId}`"
        v-model:visible="cycleDonationDialog"
        :style="{ width: '650px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
        @show="getDonations"
    >
        <p-data-table :value="donations" dataKey="id" :loading="loading">
            <p-column header="Donation Type">
                <template #body="{data}">
                    <p-dropdown
                        v-model="data.donationType"
                        :options="donationTypeOpts"
                    />
                </template>
            </p-column>

            <p-column header="Number">
                <template #body="{data}">
                    <p-input-number v-model="data.number" showButtons />
                </template>
            </p-column>

            <p-column header="Donated to">
                <template #body="{data}">
                    <p-auto-complete
                        v-model="data.donatedTo"
                        :suggestions="donatedToOpts"
                        @complete="donatedToSuggestions($event)"
                        field="demographicNo"
                        modelValue="demographicNo"
                    />
                </template>
            </p-column>

            <p-column style="min-width:8rem" bodyStyle="text-align:center">
                <template #body="{data}">
                    <p-button
                        icon="pi pi-check"
                        class="p-button-sm p-button-rounded p-button-text"
                        @click="saveDonation(data)"
                    />
                    <p-button
                        v-if="data.id"
                        icon="pi pi-times"
                        class="p-button-sm p-button-rounded p-button-text p-button-danger"
                        @click="deleteDoantionCofirm($event, data)"
                    />
                </template>
            </p-column>
        </p-data-table>
    </p-dialog>
</template>

<script>
import { inject, ref } from "vue-demi";
import { $axios } from "@/service/app-http";
import Service from "@/service/art-cycle.js";
import { useConfirm } from "primevue/useconfirm";
import { useStore } from "vuex";

export default {
    props: ["cycleId"],
    setup(props) {
        const store = useStore();
        const cycleDonationDialog = inject("cycleDonationDialog", false);
        const donations = ref([]);

        const donationTypeOpts = ["Egg", "Embryo", "Sperm"];

        const donatedTo = ref();
        const donatedToOpts = ref([]);
        const donatedToSuggestions = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "F".toLowerCase()
            }).then((resp) => {
                donatedToOpts.value = resp.data;
            });
        };

        const getDonations = () => {
            loading.value = true;
            $axios
                .get(`cycle/${props.cycleId}/donations`)
                .then((resp) => {
                    if (Array.isArray(resp.data)) {
                        store.commit("artCycle/SET_DONATIONS_CYCLE", {
                            cycleId: props.cycleId,
                            numOfDonations: resp.data.length
                        });
                    }

                    const _donations = resp.data.map((item) => {
                        return {
                            ...item,
                            donationType: item.donation_type,
                            donatedTo: item.denated_to
                        };
                    });
                    donations.value = [
                        ..._donations,
                        {
                            donationType: null,
                            number: null,
                            donatedTo: null
                        }
                    ];
                })
                .finally(() => (loading.value = false));
        };

        const loading = ref(false);
        const saveDonation = (data) => {
            loading.value = true;

            const payload = {
                ...data,
                donatedTo: data.donatedTo ? data.donatedTo.demographicNo : null
            };

            if (data.id == null) {
                $axios
                    .post(`cycle/${props.cycleId}/donations`, payload)
                    .then(() => {
                        getDonations();
                    });
            } else {
                loading.value = true;
                $axios
                    .put(`cycle/${props.cycleId}/donations/${data.id}`, payload)
                    .then(() => {
                        getDonations();
                    });
            }
        };

        const confirm = useConfirm();

        const deleteDoantionCofirm = (event, data) => {
            confirm.require({
                group: "popup",
                target: event.currentTarget,
                message: "Are you sure you want to proceed?",
                icon: "pi pi-exclamation-triangle",
                accept: () => {
                    deleteDonation(data);
                }
            });
        };

        const deleteDonation = (data) => {
            loading.value = true;
            $axios
                .delete(`cycle/${props.cycleId}/donations/${data.id}`)
                .then(() => {
                    getDonations();
                });
        };

        return {
            cycleDonationDialog,
            donations,
            donationTypeOpts,
            donatedTo,
            donatedToOpts,
            donatedToSuggestions,
            getDonations,
            loading,
            saveDonation,
            deleteDoantionCofirm
        };
    }
};
</script>

<style scoped>
:deep() .p-inputnumber-input {
    width: 60px;
}
</style>
