<template>
    <p-dialog
        header="Couples Report"
        v-model:visible="couplesReportDialog"
        :style="{ width: '500px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
        @show="getCouplesReport"
    >
        <p-textarea
            v-model="couplesReport"
            rows="20"
            style="width: 100%"
            disabled
        />
        <p-button label="Close" @click="close" />
    </p-dialog>
</template>

<script>
import { inject, ref } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const couplesReportDialog = inject("couplesReportDialog");
        const store = useStore();

        const couplesReport = ref();
        const getCouplesReport = () => {
            store.dispatch("artCycle/getCouplesReport").then((data) => {
                couplesReport.value = data;
            });
        };
        return {
            couplesReportDialog,
            couplesReport,
            getCouplesReport,
            close: () => {
                couplesReportDialog.value = false;
            }
        };
    }
};
</script>
