import { computed, inject, reactive, ref } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import Service from "@/service/art-cycle.js";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";

import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { $axios } from "@/service/app-http";
import { TIME_0, ZEBRA_PRING_URL } from "@/constants";
import { validDate } from "@/composable/validator";

export function usePatientSearch() {
    const store = useStore();

    const title = ref("Female");
    const demographic = ref();
    const fillteredDemographic = ref();
    const loading = inject("loading");

    const searchDemographic = (event) => {
        Service.searchDemographic({
            term: event.query.trim(),
            sex: "F".toLowerCase()
        }).then((resp) => {
            fillteredDemographic.value = resp.data;
        });
    };

    const onDemographicSelect = ({ value: { demographicNo } }) => {
        loading.value = true;
        store.dispatch("artCycle/getCyclesDetails", demographicNo).then(() => {
            loading.value = false;
            emitter.emit("patient-search:demographic-select");
        });
    };

    const emptyPatient = () => {
        demographic.value = null;
    };

    const printPatientAndPartner = () => {
        $axios
            .get("print/partient-parter-detail", {
                params: {
                    demograhicNo: store.state.artCycle.patient.id
                }
            })
            .then((resp) => {
                let patient = resp.data.patient;
                let partner = resp.data.partner;

                const data = {
                    patient_name: patient.firstName + patient.lastName,
                    province: "ON",
                    patient_hin: patient.hin,
                    patient_dob: patient.dob,
                    patient_sex: patient.sex,
                    patient_ver: patient.ver,
                    province: "ON",
                    partner_name: partner.firstName + partner.lastName,
                    partner_hin: partner.hin,
                    partner_dob: partner.dob,
                    partner_sex: partner.sex,
                    partner_ver: partner.ver
                };

                let url = `${ZEBRA_PRING_URL}/patient_partner_print.php?${new URLSearchParams(
                    data
                ).toString()}`;
            });
    };

    const printPatientAndDoctor = () => {
        $axios
            .get("print/partient-doctor-detail", {
                params: {
                    demograhicNo: store.state.artCycle.patient.id
                }
            })
            .then((resp) => {
                let patient = resp.data;

                const data = {
                    name: patient.firstName + patient.lastName,
                    chart: patient.domographicNo,
                    address: patient.address,
                    province: patient.province,
                    postal: patient.postal,
                    phone: patient.phone,
                    dob: patient.dob,
                    sex: patient.sex,
                    province: patient.province,
                    hin: patient.hin,
                    doctors: patient.doctorName,
                    ver: patient.ver,
                    status_date: patient.patientStatusDate
                };

                let url = `${ZEBRA_PRING_URL}/patient_doctor_print.php?${new URLSearchParams(
                    data
                ).toString()}`;
                console.log(url);
                // window.open(url);
            });
    };

    return {
        store,
        title,
        demographic,
        fillteredDemographic,
        loading,
        searchDemographic,
        onDemographicSelect,
        emptyPatient,
        printPatientAndPartner,
        printPatientAndDoctor
    };
}

