export function useCycleCreateOrEditDailogInit() {
    const store = useStore();
    const selectedCycle = computed(() => store.state.artCycle.selectedCycle);
    const cycleTypeOptions = computed(
        () => store.state.artCycle.cycleTypeOptions
    );
    const spermSourceOptions = computed(
        () => store.state.artCycle.spermSourceOptions
    );
    const eggSourceOptions = computed(
        () => store.state.artCycle.eggSourceOptions
    );

    const createOrEditDialog = inject("createOrEditDialog");
    const isEditDailog = inject("isEditDailog");

    const header = computed(() => {
        return isEditDailog.value ? "Edit" : "Create New";
    });

    const cycle = reactive({
        cycleId: null,
        cycleIdDisp: null,
        cycleType: null,
        cycleTypeDisp: null,
        cycleNoType: null,
        funded: null,
        cancelled: null,
        simulatedCycle: null,
        spermSource: null,
        eggSource: null,
        eggDonor: null,
        complete: null,
        cycleDate: null
    });

    const rules = {
        cycleDate: { validDate }
    };

    const v$ = useVuelidate(rules, cycle);
    const submitted = ref(false);

    const fillupForm = () => {
        const data = { ...store.state.artCycle.selectedCycle };
        if (isEditDailog.value) {
            cycle.cycleId = data.cycleId;
            cycle.cycleIdDisp = data.cycleIdDisp;
            cycle.cycleType = data.cycleType;
            cycle.cycleNoType = data.cycleNoType;
            cycle.cancelled = data.cancel;
            cycle.simulatedCycle = data.simulatedCycle;
            cycle.spermSource = data.spermSource;
            cycle.eggSource = data.eggSource;
            cycle.complete = data.cycleCompleted;
            cycle.ohip = data.ohip;
            cycle.surrogate = data.surrogate;
            cycle.eggDonor = data.eggDoner;
            cycle.cycleDate = data.cycleDate ? new Date(data.cycleDate) : null;
        } else {
            cycle.cycleId = null;
            cycle.cycleIdDisp = null;
            cycle.cycleNoType = null;
            cycle.cancelled = false;
            cycle.simulatedCycle = false;
            cycle.spermSource = null;
            cycle.eggSource = null;
            cycle.complete = false;
            cycle.ohip = null;
            cycle.surrogate = false;
            cycle.eggDonor = false;
            cycle.cycleDate = new Date();
        }
    };

    const loading = inject("loading");

    return {
        store,
        selectedCycle,
        cycleTypeOptions,
        spermSourceOptions,
        header,
        eggSourceOptions,
        createOrEditDialog,
        isEditDailog,
        cycle,
        fillupForm,
        loading,
        v$
    };
}

export function useMergeCycleDialogInit() {
    const store = useStore();
    const toast = useToast();
    const mergeCycleDailog = inject("mergeCycleDailog");
    const cycleSummary = computed(() => store.state.artCycle.cyclesSummary);
    const cycle1Selected = ref(null);
    const cycle2Selected = ref(null);
    const result = ref();

    const mergeCycle = (dryRun = false) => {
        if (cycle1Selected.value == null && cycle2Selected.value == null) {
            toast.add({
                severity: "error",
                summary: "Error",
                detail: "Please select 2 cycles first",
                life: 3000
            });
            return;
        }

        store
            .dispatch("artCycle/mergeCycle", {
                cycle1: cycle1Selected.value.cycleId,
                cycle2: cycle2Selected.value.cycleId,
                dryRun: dryRun
            })
            .then((data) => {
                result.value = data;
                emitter.emit("merge-cycle-dialog:merge-cycle");
            });
    };

    return {
        mergeCycleDailog,
        cycleSummary,
        cycle1Selected,
        cycle2Selected,
        mergeCycle,
        result
    };
}

export function useCryostorageReportDialog() {
    const store = useStore();
    const cryostorageReportDailog = inject("cryostorageReportDailog");
    const report = ref();
    const selectedReport = ref();
    const loading = ref(false);
    const patientSearch = ref(null);

    const pugColorOptions = computed(
        () => store.state.cryoStorage.pugColorList
    );

    const cassetteColorOptions = computed(
        () => store.state.cryoStorage.cassetteColorList
    );

    const dewarListOptions = computed(() => store.state.cryoStorage.dewarList);

    const form = reactive({
        fromDate: null,
        toDate: null,
        patientId: null,
        dewar: null,
        cassetteColor: null,
        plugColor: null,
        freezId: null
    });
    const submitted = ref(false);
    const rules = {
        fromDate: { required },
        toDate: { required }
    };
    const v$ = useVuelidate(rules, form);
    const getReport = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }
        loading.value = true;

        store
            .dispatch("cryoStorage/getCryoStorageListFiltered", {
                ...form,
                fromDate: format(form.fromDate, DATE_FORMAT_STRING),
                toDate: format(form.toDate, DATE_FORMAT_STRING),
                patientId: patientSearch.value
                    ? patientSearch.value.demographicNo
                    : null
            })
            .then((data) => {
                report.value = data;
            })
            .finally(() => {
                loading.value = false;
            });
    };

    const emb = reactive({
        _1g: null,
        _1s: null,
        _2g: null,
        _2s: null,
        _3g: null,
        _3s: null,
        _4g: null,
        _4s: null
    });

    const fillEmbForm = ({ data }) => {
        emb._1g = data.embryo1g;
        emb._1s = data.embryo1s;
        emb._2g = data.embryo2g;
        emb._2s = data.embryo2s;
        emb._3g = data.embryo3g;
        emb._3s = data.embryo3s;
        emb._4g = data.embryo4g;
        emb._4s = data.embryo4s;
    };

    return {
        store,
        report,
        loading,
        cryostorageReportDailog,
        pugColorOptions,
        cassetteColorOptions,
        dewarListOptions,
        form,
        submitted,
        v$,
        getReport,
        patientSearch,
        selectedReport,
        emb,
        fillEmbForm
    };
}
