<template>
    <p-dialog
        :header="header"
        v-model:visible="createOrEditDialog"
        :style="{ width: '400px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
        @show="fillupForm"
    >
        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle Type
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.cycleType"
                    id="cycle-type"
                    :options="cycleTypeOptions"
                    @change="cycleTypeChange"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle No Type
            </label>
            <div class="p-col">
                <p-input-text v-model="cycle.cycleNoType" id="cycle-no-type" />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle Date
            </label>
            <div class="p-col">
                <p-calendar
                    v-model="cycle.cycleDate"
                    id="cycle-date"
                    date-format="mm/dd/yy"
                    :class="{
                        'p-invalid': v$.cycleDate.$invalid
                    }"
                />
                <i
                    v-if="v$.cycleDate.validDate.$invalid"
                    class="pi pi-info-circle p-mt-2"
                    v-tooltip.right="v$.cycleDate.validDate.$message"
                    style="color: #f44336; margin-left: 5px;"
                ></i>
            </div>
        </div>

        <div class="p-field p-grid" v-if="cycle.cycleType == 'FET'">
            <label for="firstname" class="p-col-4 p-mb-0">
                Surrogate
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.surrogate"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Funded
            </label>

            <div class="p-col">
                <p-dropdown
                    v-model="cycle.ohip"
                    :options="fundedOptions"
                    optionLabel="label"
                    optionValue="value"
                />
                <!-- <p-toggle-button
                    v-model="cycle.ohip"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                /> -->
            </div>
        </div>

        <div style="display: flex; align-items: center;" class="p-mt-2">
            <div
                class="p-column p-grid p-mr-4"
                v-if="isEditDailog"
                style="align-items: center;"
            >
                <label for="cycle-cancelled" class="p-col-9 p-mb-0">
                    Cycle Cancelled
                </label>
                <div class="p-col-3">
                    <p-toggle-button
                        v-model="cycle.cancelled"
                        @change="cycleCancelChange"
                        id="cycle-cancelled"
                        onIcon="pi pi-check"
                        offIcon="pi pi-times"
                    />
                </div>
            </div>

            <div
                class="p-column p-grid"
                v-if="isEditDailog"
                style="align-items: center;"
            >
                <label for="cycle-complete" class="p-col-9 p-mb-0">
                    Completed
                </label>
                <div class="p-col-3">
                    <p-toggle-button
                        v-model="cycle.complete"
                        @change="cycleCompleteChange"
                        id="cycle-complete"
                        onIcon="pi pi-check"
                        offIcon="pi pi-times"
                    />
                </div>
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="simulated-cycle" class="p-col-4 p-mb-0">
                Simulated Cycle
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.simulatedCycle"
                    id="simulated-cycle"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="cycle.cycleType != 'Egg Donor'">
            <label for="firstname" class="p-col-4 p-mb-0">
                Sperm Source
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.spermSource"
                    id="cycle-type"
                    :filter="true"
                    :options="spermSourceOptions"
                    optionLabel="label"
                    optionValue="value"
                    :showClear="true"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Egg Source
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.eggSource"
                    id="cycle-type"
                    :filter="true"
                    :options="eggSourceOptions"
                    optionLabel="label"
                    optionValue="value"
                    :showClear="true"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Egg Donor
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.eggDonor"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <!-- <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Complete
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.complete"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div> -->

        <div class="p-grid p-mt-4">
            <p-button
                label="Edit"
                class="p-mr-2"
                @click="editCycle"
                v-if="isEditDailog"
                :loading="loading"
            />
            <p-button
                label="Save"
                class="p-mr-2"
                @click="createCycle"
                v-if="!isEditDailog"
                :loading="loading"
            />

            <p-button
                label="Cancel"
                class="p-button-danger p-button-outlined"
                @click="createOrEditDialog = false"
            />
        </div>
    </p-dialog>
</template>

<script>
import { onMounted, ref } from "vue";
import { emitter } from "@/event-bus";
import { useCycleCreateOrEditDailogInit } from "@/composable/art-cycle";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const {
            store,
            selectedCycle,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            header,
            cycle,
            fillupForm,
            loading,
            createOrEditDialog,
            isEditDailog,
            v$
        } = useCycleCreateOrEditDailogInit();

        const cycleTypeChange = () => {
            if (cycle.cycleType === "Cancelled") {
                cycle.cancelled = true;
            } else {
                cycle.cancelled = false;
            }
        };

        const cycleCancelChange = () => {
            if (cycle.cancelled === true) {
                cycle.complete = false;
            }
        };

        const cycleCompleteChange = () => {
            if (cycle.complete === true) {
                cycle.cancelled = false;
            }
        };

        const editCycle = () => {
            if (v$.value.$invalid) return;

            if (cycle.cycleType == "Egg Donor") {
                cycle.spermSource = null;
            }

            const born = {
                pNumber: store.state.artCycle.patient.id,
                createdBy: store.state.user.username,
                updatedBy: store.state.user.username,
                parentCycleType: store.state.artCycle.selectedCycle.cycleType,
                firstName: store.state.artCycle.patient.firstName,
                lastName: store.state.artCycle.patient.lastName,
                sex: "f",
                yearOfBirth: store.state.artCycle.patient.year,
                monthOfBirth: store.state.artCycle.patient.month,
                dateOfBirth: store.state.artCycle.patient.day
            };

            loading.value = true;
            store
                .dispatch("artCycle/updateCycle", {
                    ...born,
                    cycleNo: selectedCycle.value.cycleNo,
                    donnations: selectedCycle.value.donnations,
                    cycleId: selectedCycle.value.cycleId,
                    cycleType: cycle.cycleType,
                    cycleNoType: cycle.cycleNoType,
                    spermSource:
                        cycle.cycleType != "Investigative"
                            ? cycle.spermSource
                            : "",
                    eggSource:
                        cycle.cycleType != "Investigative"
                            ? cycle.eggSource
                            : "",
                    eggDonor:
                        cycle.cycleType != "Investigative"
                            ? cycle.eggDonor
                            : false,
                    cycleIncomplete: !cycle.complete,
                    cycleComplete: !!cycle.complete,
                    ohip: cycle.ohip,
                    surrogate: cycle.surrogate,
                    cycleCancelled: !!cycle.cancelled,
                    simulatedCycle: !!cycle.simulatedCycle,
                    others: cycle.others,
                    cycleDate: cycle.cycleDate
                        ? format(cycle.cycleDate, DATE_FORMAT_STRING)
                        : null
                })
                .then(() => {
                    createOrEditDialog.value = false;
                    return store.dispatch(
                        "artCycle/getCyclesDetails",
                        store.state.artCycle.patient.id
                    );
                })
                .catch(() => {})
                .finally(() => {
                    loading.value = false;
                });
        };

        const toast = useToast();

        const fundedOptions = ref([
            { label: "Yes", value: true },
            { label: "No", value: false }
        ]);

        const createCycle = () => {
            if (v$.value.$invalid) return;

            if (cycle.cycleType == "" || cycle.cycleType == null) {
                toast.add({
                    severity: "warn",
                    summary: "Error",
                    detail: "Cycle Type is required",
                    life: 3000
                });

                return;
            }

            if (cycle.ohip == null) {
                toast.add({
                    severity: "warn",
                    summary: "Error",
                    detail: "Funded field is required",
                    life: 3000
                });

                return;
            }

            if (
                cycle.cycleType != "Egg Donor" &&
                cycle.cycleType != "FET" &&
                (!cycle.spermSource ||
                    cycle.spermSource == "" ||
                    cycle.spermSource == null)
            ) {
                toast.add({
                    severity: "warn",
                    summary: "Error",
                    detail: "Sperm Source is required",
                    life: 3000
                });

                return;
            }

            if (cycle.cycleType == "Egg Donor") {
                cycle.spermSource = null;
            }

            if (
                cycle.cycleType != "FET" &&
                (!cycle.eggSource ||
                    cycle.eggSource == "" ||
                    cycle.eggSource == null)
            ) {
                toast.add({
                    severity: "warn",
                    summary: "Error",
                    detail: "Egg Source is required",
                    life: 3000
                });

                return;
            }

            loading.value = true;

            const born = {
                pNumber: store.state.artCycle.patient.id,
                createdBy: store.state.user.username,
                updatedBy: store.state.user.username,
                parentCycleType: cycle.cycleType,
                firstName: store.state.artCycle.patient.firstName,
                lastName: store.state.artCycle.patient.lastName,
                sex: "f",
                yearOfBirth: store.state.artCycle.patient.year,
                monthOfBirth: store.state.artCycle.patient.month,
                dateOfBirth: store.state.artCycle.patient.day
            };

            const _cycle = {
                ...born,
                patientId: store.state.artCycle.patient.id,
                cycleNo: store.getters["artCycle/latestCycleId"] + 1,
                cycleNumber: store.getters["artCycle/latestCycleId"] + 1,
                cycleType: cycle.cycleType,
                donnations: cycle.donnations,
                cycleNoType: cycle.cycleNoType,
                spermSource:
                    cycle.cycleType != "Investigative" ? cycle.spermSource : "",
                eggSource:
                    cycle.cycleType != "Investigative" ? cycle.eggSource : "",
                cycleDate: cycle.cycleDate
                    ? format(cycle.cycleDate, DATE_FORMAT_STRING)
                    : null,
                cycleSucceeded: true,
                cycleExpired: false,
                // eggDonor: cycle.cycleType != "Investigative" ? true : false,
                eggDonor: false,
                ohip: cycle.ohip,
                surrogate: cycle.surrogate
            };

            store.dispatch("artCycle/createNewCycle", _cycle).then(() => {
                loading.value = false;
                createOrEditDialog.value = false;
            });
        };

        onMounted(() => {
            if (selectedCycle.value) {
                fillupForm(selectedCycle.value);
            }
            emitter.on("cycle-table:save-cycle", editCycle);
        });

        onMounted(() => {
            emitter.off("cycle-table:save-cycle", editCycle);
        });

        return {
            header,
            isEditDailog,
            createOrEditDialog,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle,
            editCycle,
            fundedOptions,
            createCycle,
            fillupForm,
            loading,
            v$,
            cycleTypeChange,
            cycleCancelChange,
            cycleCompleteChange
        };
    }
};
</script>

<style scoped>
.p-dropdown {
    width: 100%;
}
</style>
