<template>
    <p-dialog
        header="Merge Cycle"
        v-model:visible="mergeCycleDailog"
        :style="{ width: '80%' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-grid">
            <div class="p-col-8">
                <div class="p-grid">
                    <p-data-table
                        :value="cycleSummary"
                        dataKey="cycleId"
                        v-model:selection="cycle1Selected"
                        selectionMode="single"
                        showGridlines
                        class="p-datatable-sm p-col"
                    >
                        <template #header> Cycle 1 </template>

                        <p-column field="cycleId" header="#" />
                        <p-column field="cycleNo" header="No" />
                        <p-column field="cycleType" header="Type" />
                        <p-column field="cycleDate" header="Date">
                            <template #body="{ data }">
                                {{
                                    data.cycleDate
                                        ? data.cycleDate.split(" ")[0]
                                        : data.cycleDate
                                }}
                            </template>
                        </p-column>
                    </p-data-table>

                    <p-data-table
                        :value="cycleSummary"
                        dataKey="cycleId"
                        v-model:selection="cycle2Selected"
                        selectionMode="single"
                        showGridlines
                        class="p-datatable-sm p-col"
                    >
                        <template #header> Cycle 1 </template>

                        <p-column field="cycleId" header="#" />
                        <p-column field="cycleNo" header="No" />
                        <p-column field="cycleType" header="Type" />
                        <p-column field="cycleDate" header="Date">
                            <template #body="{ data }">
                                {{
                                    data.cycleDate
                                        ? data.cycleDate.split(" ")[0]
                                        : data.cycleDate
                                }}
                            </template>
                        </p-column>
                    </p-data-table>
                </div>

                <div class="p-field">
                    <label><strong>Result</strong></label>
                    <p-textarea
                        v-model="result"
                        style="width: 100%"
                        rows="20"
                        disabled
                    />
                </div>
            </div>
            <div class="p-col-4">
                <p-scroll-panel style="height: 470px">
                    <p>How to merge:</p>
                    <p>
                        Merge tries to add data from cycle 1 to cycle 2. After
                        merging data, cycle 2 is removed.<br />First, you should
                        select cycle 1 and cycle 2 from the lists and click Dry
                        Run button. Dry run checks out data, discovers possible
                        conflicts, and builds the report.It does not modify data
                        in database, so you are free to make dry run as many
                        times as yoyu want.<br />For monitor record, System
                        compares monitor fields in cycle 1 and cycle 2. If field
                        is set cycle 1 only, System keeps it. If field is set in
                        cycle 2 and not set in cycle 1, System copies field
                        value from cycle 2 to cycle 1. If field is set both in
                        cycle 1 and cycle 2, System reports a conflict.<br />For
                        monitor lines, System tries to add lines from cycle 2 to
                        cycle 1. If monitor line exists for the same date both
                        in cycles 1 and 2, System reports a conflict.<br />For
                        cryostorage, System checks header existence in cycle 1
                        and cycle 2. If header exists in cycle 1 only, System
                        keeps it. If header exists in cycle 2 and not in cycle
                        1, System moves header from cycle 2 to cycle 1. If
                        header exists both in cycle 1 and in cycle 2, System
                        reports a conflict.<br />For cryostorage lines, System
                        always adds all lines from cycle 2 to cycle 1.<br />For
                        all other records, like embryo transfer, OPU and
                        pregnancy outcome, System checks header existence in
                        cycle 1 and cycle 2. If header exists in cycle 1 only,
                        System keeps it. If header exists in cycle 2 and not in
                        cycle 1, System moves header from cycle 2 to cycle 1. If
                        header exists both in cycle 1 and in cycle 2, System
                        reports a conflict.<br />For lines attached to these
                        records, if System keeps header for cycle 1, it also
                        keeps lines for cycle1. If System moves header from
                        cycle 2 to cycle1, System removes lines from cycle 1 and
                        then moves lines from cycle 2 to cycle 1.
                    </p>
                    <p>
                        If a conflict is reported, you have to resolve the
                        conflict manually. When merging data, System will skip
                        the record where conflict is not resolved, so you may
                        loose data in this case!
                        <br />For instance, if both cycles have embryo transfer
                        data, dry run report will mention you have a conflict.To
                        resolve it,you have 2 choices:<br />1. Copy necessary
                        header data from cycle 2 to cycle 1, copy lines from
                        cycle 2 to cycle 1, and then remove header for cycle
                        2.<br />2. Copy necessary header data from cycle 1 to
                        cycle 2, copy lines from cycle 1 to cycle 2, and then
                        remove header for cycle 1.<br />After that, if you make
                        dry run again, it should not fine this conflict anymore.
                    </p>
                    <p>
                        When all conflicts are resolved, you may click Merge
                        button. System will merge data, store merged data in
                        database and remove cycle2. And you can run merge only
                        once, so be careful!
                    </p>
                </p-scroll-panel>
            </div>
        </div>

        <p-button label="Dry Run" class="p-mr-2" @click="dryRun" />
        <p-button label="Merge" class="p-mr-2" @click="mergeCycle" />
        <p-button label="Close" @click="close" />
    </p-dialog>
</template>

<script>
import { useMergeCycleDialogInit } from "@/composable/art-cycle";
export default {
    setup() {
        const {
            mergeCycleDailog,
            cycleSummary,
            cycle1Selected,
            cycle2Selected,
            mergeCycle,
            result
        } = useMergeCycleDialogInit();

        return {
            mergeCycleDailog,
            cycleSummary,
            cycle1Selected,
            cycle2Selected,
            result,
            dryRun: () => {
                mergeCycle(true);
            },
            mergeCycle: () => {
                mergeCycle();
            },
            close: () => {
                mergeCycleDailog.value = false;
            }
        };
    }
};
</script>
