<template>
    <p-data-table
        :value="records"
        dataKey="cycleId"
        class="p-datatable-sm p-shadow-1"
        :scrollable="true"
        scrollHeight="flex"
        showGridlines
    >
        <p-column-group type="header">
            <p-row>
                <p-column header="Existing records from cycles" colspan="4" />
            </p-row>

            <p-row>
                <p-column header="Cycle Type" />
                <p-column header="Cycle Id" />
                <p-column header="#" />
                <p-column header="Action" />
            </p-row>
        </p-column-group>

        <p-column field="cycleType" header="Cycle Type" />
        <p-column field="cycleId" header="Cycle Id" />
        <p-column field="cycleNo" header="#" />
        <p-column header="Action" />
    </p-data-table>
</template>

<script>
import { reactive } from "vue";
export default {
    setup() {
        const records = reactive([]);

        return { records };
    }
};
</script>

