<template>
    <main id="art-cycle" class="p-m-2 p-input-filled p-inputtext-sm">
        <div class="patient-search-area">
            <div>
                <art-cycle-patient-search />
            </div>
            <div>
                <art-cycle-partner-search />
            </div>
        </div>

        <div class="art-cycle">
            <art-cycle-summary />
        </div>

        <!-- This section will stay at the bottom on desktop and mobile with a fixed height and 5px bottom margin -->
        <div class="lab-container">
            <div class="lab-patient">
                <consult-report />
            </div>
        </div>

        <!-- <p-button type="button" label="Save" class="p-mr-2"></p-button> -->
        <art-cycle-create-or-edit-dialog />
        <art-cycle-merge-dialog />
        <order-blood-test-confirm-dialog />
        <order-blood-test-dialog />
        <cryostorage-report-dialog />
        <couples-report-dialog />
    </main>
</template>

<script>
import { computed, onMounted, provide, ref } from "vue";
import { useStore } from "vuex";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";

import ArtCyclePatientSearch from "@/components/art-cycle/patient-search.vue";
import ArtCyclePartnerSearch from "@/components/art-cycle/partner-search.vue";
import ArtCycleSummary from "@/components/art-cycle/summary.vue";
import ArtCycleForm from "@/components/art-cycle/form.vue";
import ArtCycleCreateOrEditDialog from "@/components/art-cycle/create-edit-dialog.vue";
// import ArtCycleLabTest from "@/components/art-cycle/lab-test.vue";
import ConsultReport from "@/components/art-cycle/consult-report.vue";
import ArtCycleExistingCycleRecords from "@/components/art-cycle/existing-cycle-records.vue";
import ArtCycleBornCycleInformations from "@/components/art-cycle/born-cycle-information.vue";
import ArtCyclePatientDetailForm from "@/components/art-cycle/patient-detail-form.vue";
import ArtCycleSharedTest from "@/components/born/obstetrical-history-form.vue";
import BornComplicationForm from "@/components/born/complicaitons-form.vue";
import ArtCycleMergeDialog from "@/components/art-cycle/merge-dialog.vue";
import OrderBloodTestConfirmDialog from "@/components/blood-tests/confirm-dialog.vue";
import OrderBloodTestDialog from "@/components/blood-tests/dialog.vue";
import CryostorageReportDialog from "@/components/cryo-storage/cyrostorage-report-dialog.vue";
import CouplesReportDialog from "@/components/art-cycle/couples-report-dialog.vue";

export default {
    components: {
        ArtCyclePatientSearch,
        ArtCyclePartnerSearch,
        ArtCycleSummary,
        ArtCycleForm,
        ArtCycleCreateOrEditDialog,
        // ArtCycleLabTest,
        ConsultReport,
        ArtCycleExistingCycleRecords,
        ArtCycleBornCycleInformations,
        ArtCyclePatientDetailForm,
        ArtCycleSharedTest,
        BornComplicationForm,
        ArtCycleMergeDialog,
        OrderBloodTestDialog,
        OrderBloodTestConfirmDialog,
        CryostorageReportDialog,
        CouplesReportDialog
    },
    setup() {
        const store = useStore();
        const loading = ref(false);
        provide("loading", loading);

        const cyclesSummary = computed(
            () => store.state.artCycle.cyclesSummary
        );

        const patient = ref();
        const partner = ref();
        const cycleTypes = ref();
        const createOrEditDialog = ref(false);
        const isEditDailog = ref(false);

        provide("createOrEditDialog", createOrEditDialog);
        provide("isEditDailog", isEditDailog);
        provide("cycles", cyclesSummary);
        provide("patient", patient);
        provide("partner", partner);
        provide("cycleTypes", cycleTypes);

        const orderBloodTestDialog = ref(false);
        const orderBloodTestConfirmDialog = ref(false);
        const isCycle = ref(0);

        provide("orderBloodTestDialog", orderBloodTestDialog);
        provide("orderBloodTestConfirmDialog", orderBloodTestConfirmDialog);
        provide("isCycle", isCycle);

        const mergeCycleDailog = ref(false);
        provide("mergeCycleDailog", mergeCycleDailog);

        const cryostorageReportDailog = ref(false);
        provide("cryostorageReportDailog", cryostorageReportDailog);

        const couplesReportDialog = ref(false);
        provide("couplesReportDialog", couplesReportDialog);

        onMounted(() => {
            store.dispatch("provider/getProviders").catch(() => {});

            store
                .dispatch("dailyReport/getOrderList", {
                    fromDate: format(new Date(), DATE_FORMAT_STRING),
                    toDate: format(new Date(), DATE_FORMAT_STRING),
                    providerNo: store.state.user.providerNo
                })
                .catch(() => {});
        });

        return {
            cyclesSummary
        };
    }
};
</script>

<style>
#art-cycle .p-card .p-card-content {
    padding: 0;
}

/* Main container to make the art-cycle section take up remaining space on desktop */
.art-cycle {
    /* display: flex; */
    flex-direction: column;
    height: calc(
        (100vh - 182px) * 0.67
    ); /* Adjust height for desktop screens */
    margin-bottom: 50px; /* Ensures 5px margin for bottom of page */
}

/* Lab container with fixed height at the bottom of the page */
.lab-container {
    position: relative;
    right: 5px;
    left: 5px;
    height: calc((100vh - 182px) * 0.32);
}

.patient-search-area {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;
}

.patient-search-area > div {
    flex: 1; /* Distributes space equally, each div takes 50% */
}

/* Ensures the content inside .lab-container stays 5px from the bottom */
.lab-patient {
    height: 100%;
}

@media (max-width: 768px) {
    .lab-patient {
        margin-top: 0px; /* Custom margin for mobile, if needed */
    }
    .art-cycle {
        height: auto; /* Adjust height for mobile screens */
    }

    .lab-container {
        position: inherit;
        height: auto;
    }

    .patient-search-area {
        display: inline;
    }

    .patient-search-area > div {
        flex: 0; /* Distributes space equally, each div takes 50% */
    }
}
</style>
