<template>
    <p-dialog
        header="Born Validation Error"
        v-model:visible="bornCsvErrorDailog"
        :style="{ width: '600px' }"
        :modal="true"
    >
        <div v-for="(cycle, index) in bornCsvErrors" :key="index">
            <div>
                Cycle ID: <strong>{{ cycle.cycleId }}</strong>
            </div>
            <template v-if="cycle.errors">
                <div v-for="(error, idx) in cycle.errors" :key="idx">
                    <small style="color: red;">{{ error }}</small>
                </div>
            </template>
            <hr />
        </div>

        <template #footer>
            <p-button
                label="Cancel"
                class="p-mr-1 p-button-outlined p-button-danger"
                @click="bornCsvErrorDailog = false"
            />
            <!-- <p-button
                label="Download Anyway"
                class="p-mr-1 p-button-help"
                @click="getBornCSV"
            /> -->

            <p-button
                label="Mark as Completed"
                class="p-mr-1 p-button-info"
                @click="markAsComplete"
            />

            <p-button
                label="Mark as Uncompleted"
                class="p-mr-1 p-button-info"
                @click="markAsUncomplete"
            />

            <p-button
                label="Download"
                class="p-mr-1 p-button-help"
                @click="getBornCSV"
            />
        </template>
    </p-dialog>
</template>

<script>
import { inject } from "vue";
import { useStore } from "vuex";
import { ORIGIN } from "@/config";
import {
    getBornCSV as getBornCSVService,
    markBornAsCompleteService,
    markBornAsUncompleteService
} from "@/service/art-cycle";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const bornCsvErrorDailog = inject("bornCsvErrorDailog");
        const bornCsvErrors = inject("bornCsvErrors");

        const getBornCSV = async () => {
            emitter.emit("born-csv-error-diallog:get-bron-csv");
        };

        const markAsComplete = async () => {
            const { csv } = await markBornAsCompleteService(
                store.state.artCycle.patient.id,
                bornCsvErrors.value[0].cycleId
            );

            store
                .dispatch(
                    "artCycle/getCyclesDetails",
                    store.state.artCycle.patient.id
                )
                .then(() => {
                    bornCsvErrorDailog.value = false;
                });
        };

        const markAsUncomplete = async () => {
            const { csv } = await markBornAsUncompleteService(
                store.state.artCycle.patient.id,
                bornCsvErrors.value[0].cycleId
            );

            store
                .dispatch(
                    "artCycle/getCyclesDetails",
                    store.state.artCycle.patient.id
                )
                .then(() => {
                    bornCsvErrorDailog.value = false;
                });
        };

        return {
            bornCsvErrorDailog,
            bornCsvErrors,
            getBornCSV,
            markAsComplete,
            markAsUncomplete
        };
    }
};
</script>
